.main-section ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: justify;
  padding: 0px;
}
.main-section ul li {
  margin: 1%;
}
#main-section a {
  color: #000000;
}

#movie-search {
  text-align: center;
  margin: auto;
  display: block;
  width: 100%;
  padding: 3px;
}

@media screen and (min-width: 800px) {
  #movie-search {
    margin: 0px 250px;
    position: absolute;
    margin-top: -42px;
    width: 250px;
    padding: 5px;
  }
}
